.work{
    padding: 10em 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    @include responsive(l){
    padding: 6em 0;
    }
}

// Appear
.container_appear{
    border-radius: 1em;
    // padding-top: 2em;
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: $color5;
    @include responsive(m){
    width: 65%;
    }
    @include responsive(s){
    width: 70%;
    }
    @include responsive(xs){
    width: 85%;
    }

    .work_appear{
        padding: 4em 0;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        img{
            width: 30%;
            @include responsive(xl){
            width: 35%;
            }
            @include responsive(l){
            width: 40%;
            }
            @include responsive(xs){
            width: 50%;
            }
        }
        &_name{
        margin-top: 1em;
        font-size: 40px;
        color: $color4;
        @include responsive(s){
        font-size: 30px;
        }
        }
        &_link{
            margin-top: 1em;
            display: flex;
            justify-content: center;
            gap: 1em;
            li{
                list-style: none;
                path{
                    fill: $color4;
                }
            }
        }
    }
}


// Doc
.container_doc{
    width: 60%;

    .WorkDoc{
        display: flex;
        gap: 1em;
        grid-template-columns: auto auto auto;
        justify-content: space-between;

        &_Container{
            color: $color4;
            padding: 2em 1em 3em 0;
            border-radius: 5px;
            background-color: $color5;
            padding-left: 10px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            &_Title{
            color: $color4;
            }
            &_Desc{
            color: $color4;
            }
            &_Details{
            color: $color4;
            }
        }

    }
}


// Divider
.h-divider {
    margin: auto;
    width: 80%;
    position: relative;
  }
  
  .h-divider .shadow {
    overflow: hidden;
    height: 20px;
  }
  
  .h-divider .shadow:after {
    content: '';
    display: block;
    margin: -25px auto 0;
    width: 100%;
    height: 25px;
    border-radius: 125px/12px;
    box-shadow: 0 0 8px $color1;
  }