.me{
  padding-top: 10em;
  @include responsive(l){
  padding-top: 6em;
  }
}

// About
.about{
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2em;
    @include responsive(hd){
      gap: 0;
    }
    @include responsive(l){
      margin: 0 11em;
    }
    @include responsive(m){
      margin: 0 6em;
    }
    @include responsive(s){
      margin: 0 4em;
    }
    @include responsive(xs){
      margin: 0 2em;
    }

    &_img{
        display: flex;
        align-items: center;
        justify-content: center;
        img{
            border-radius: 1em; 
            box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
            width: 90%;
            filter: grayscale(50%);
            @include responsive(hd){
              width: 75%;
            }
            @include responsive(l){
              width: 100%;
            }
        }
    }

    &_desc{
        word-spacing: 2px;
        display: flex;
        width: 500px;
        flex-direction: column;
        justify-content: flex-start;
        color: $color4;
        @include responsive(xl){
          width: 480px;
        }
        @include responsive(l){
          margin-top: 2em;
          width: 100%;
        }

        &_name{
            text-align: left;
            color: $color3;
            font-size: 70px;
            font-weight: 800;
            @include responsive(hd){
            font-size: 60px;
            }
            @include responsive(xs){
            font-size: 40px;
            }
            @include responsive(l){
            font-size: 50px;
            }
        }

        p{  
            line-height:25px;
            font-size: 17px;
            @include responsive(xl){
              font-size: 17px;
            }
            @include responsive(l){
              font-size: 20px;
            }
            @include responsive(xs){
            font-size: 17px;
            margin-top: -5px;
            }
        }

        .skill{
            display: flex;
            gap: 3em;
            ul{
              margin-top: -2px;
                li{
                    margin-top: 5px;
                    font-size: 18px;
                    list-style: none;
                    @include responsive(xl){
                      font-size: 17px;
                    }
                    @include responsive(l){
                      font-size: 20px;
                    }
                    @include responsive(xs){
                      font-size: 17px;
                    }
                }
            }
        }

    }
}

